<template>
  <div class="vue-container">
    <VueLink :lines="lines" />
    <VueBlock
      v-for="block in blocks"
      :key="block.id"
      v-bind.sync="block"
      :options="optionsForChild"
      @update="updateScene"
      @linkingStart="linkingStart(block, $event)"
      @linkingStop="linkingStop(block, $event)"
      @linkingBreak="linkingBreak(block, $event)"
      @select="blockSelect(block)"
      @delete="blockDelete(block)"
    />
    <div class="btn-zoom">
      <i class="icon icon-plus" @click="zoom(1)"></i>
      <i class="icon icon-minimize" @click="zoom(0)"></i>
      <i class="icon icon-minus" @click="zoom(-1)"></i>
    </div>
  </div>
</template>

<script>
import merge from "deepmerge";
import mouseHelper from "./helpers/mouse";

import VueBlock from "./VueBlock";
import VueLink from "./VueLink";

export default {
  name: "VueBlockContainer",
  components: {
    VueBlock,
    VueLink,
  },
  props: {
    blocksContent: {
      type: Array,
      default() {
        return [];
      },
    },
    scene: {
      type: Object,
      default: () => ({ blocks: [], links: [], container: {} }),
    },
    options: {
      type: Object,
    },
  },
  mounted() {
    document.documentElement.addEventListener(
      "mousemove",
      this.handleMove,
      true
    );
    document.documentElement.addEventListener(
      "mousedown",
      this.handleDown,
      true
    );
    document.documentElement.addEventListener("mouseup", this.handleUp, true);
    document.documentElement.addEventListener("wheel", this.handleWheel, true);

    this.centerX = this.$el.clientWidth / 2;
    this.centerY = this.$el.clientHeight / 2;

    this.importBlocksContent();
    this.importScene();
  },
  beforeDestroy() {
    document.documentElement.removeEventListener(
      "mousemove",
      this.handleMove,
      true
    );
    document.documentElement.removeEventListener(
      "mousedown",
      this.handleDown,
      true
    );
    document.documentElement.removeEventListener(
      "mouseup",
      this.handleUp,
      true
    );
    document.documentElement.removeEventListener(
      "wheel",
      this.handleWheel,
      true
    );
  },
  created() {
    this.mouseX = 0;
    this.mouseY = 0;

    this.lastMouseX = 0;
    this.lastMouseY = 0;

    this.minScale = 0.2;
    this.maxScale = 5;

    this.linking = false;
    this.linkStartData = null;

    this.inputSlotClassName = "inputSlot";

    this.defaultScene = {
      blocks: [],
      links: [],
      container: {},
    };
  },
  data() {
    return {
      dragging: false,
      //
      centerX: 0,
      centerY: 0,
      scale: 1,
      //
      nodes: [],
      blocks: [],
      links: [],
      //
      tempLink: null,
      selectedBlock: null,
      hasDragged: false,
    };
  },
  computed: {
    optionsForChild() {
      return {
        width: 200,
        titleHeight: 48,
        scale: this.scale,
        inputSlotClassName: this.inputSlotClassName,
        center: {
          x: this.centerX,
          y: this.centerY,
        },
      };
    },
    container() {
      return {
        centerX: this.centerX,
        centerY: this.centerY,
        scale: this.scale,
      };
    },
    // Links calculate
    lines() {
      let lines = [];

      for (let link of this.links) {
        let originBlock = this.blocks.find((block) => {
          return block.id === link.originID;
        });

        let targetBlock = this.blocks.find((block) => {
          return block.id === link.targetID;
        });

        if (!originBlock || !targetBlock) {
          console.log("Remove invalid link", link);
          this.removeLink(link.id);
          continue;
        }

        if (originBlock.id === targetBlock.id) {
          console.log("Loop detected, remove link", link);
          this.removeLink(link.id);
          continue;
        }

        let originLinkPos = this.getConnectionPos(
          originBlock,
          link.originSlot,
          false
        );
        let targetLinkPos = this.getConnectionPos(
          targetBlock,
          link.targetSlot,
          true
        );

        if (!originLinkPos || !targetLinkPos) {
          console.log("Remove invalid link (slot not exist)", link);
          this.removeLink(link.id);
          continue;
        }

        let x1 = originLinkPos.x;
        let y1 = originLinkPos.y;

        let x2 = targetLinkPos.x;
        let y2 = targetLinkPos.y;

        lines.push({
          x1: x1,
          y1: y1,
          x2: x2,
          y2: y2,
          slot: link.originSlot,
          style: {
            stroke: "rgb(101, 185, 244)",
            strokeWidth: 3 * this.scale,
            fill: "none",
          },
          outlineStyle: {
            stroke: "#666",
            strokeWidth: 6 * this.scale,
            strokeOpacity: 0.6,
            fill: "none",
          },
        });
      }

      if (this.tempLink) {
        this.tempLink.style = { // eslint-disable-line
          stroke: "#8f8f8f",
          strokeWidth: 3 * this.scale,
          fill: "none",
        };

        lines.push(this.tempLink);
      }

      return lines;
    },
  },
  methods: {
    zoom(value) {
      if (value === 0) {
        this.scale = 1;
        return;
      }
      let deltaScale = value === 1 ? 1.1 : 0.9090909090909091;
      this.scale *= deltaScale;
      // this.scale = (value === 1) ? this.scale + 0.1 : this.scale - 0.1;
      if (this.scale < this.minScale) {
        this.scale = this.minScale;
        return;
      } else if (this.scale > this.maxScale) {
        this.scale = this.maxScale;
        return;
      }
      let zoomingCenter = {
        x: this.mouseX,
        y: this.mouseY,
      };

      let deltaOffsetX = (zoomingCenter.x - this.centerX) * (deltaScale - 1);
      let deltaOffsetY = (zoomingCenter.y - this.centerY) * (deltaScale - 1);

      this.centerX -= deltaOffsetX;
      this.centerY -= deltaOffsetY;

      this.updateScene();
    },
    handleMove(e) {
      let mouse = mouseHelper.getMousePosition(this.$el, e);
      this.mouseX = mouse.x;
      this.mouseY = mouse.y;

      if (this.dragging) {
        let diffX = this.mouseX - this.lastMouseX;
        let diffY = this.mouseY - this.lastMouseY;

        this.lastMouseX = this.mouseX;
        this.lastMouseY = this.mouseY;

        this.centerX += diffX;
        this.centerY += diffY;

        this.hasDragged = true;
      }

      if (this.linking && this.linkStartData) {
        let linkStartPos = this.getConnectionPos(
          this.linkStartData.block,
          this.linkStartData.slotNumber,
          false
        );
        this.tempLink = {
          x1: linkStartPos.x,
          y1: linkStartPos.y,
          x2: this.mouseX,
          y2: this.mouseY,
          slot: this.linkStartData.slotNumber
        };
      }
    },
    handleDown(e) {
      const target = e.target || e.srcElement;
      if (
        (target === this.$el || target.matches("svg, svg *")) &&
        e.which === 1
      ) {
        this.dragging = true;

        let mouse = mouseHelper.getMousePosition(this.$el, e);
        this.mouseX = mouse.x;
        this.mouseY = mouse.y;

        this.lastMouseX = this.mouseX;
        this.lastMouseY = this.mouseY;

        this.deselectAll();
        if (e.preventDefault) e.preventDefault();
      }
    },
    handleUp(e) {
      const target = e.target || e.srcElement;

      if (this.dragging) {
        this.dragging = false;

        if (this.hasDragged) {
          this.updateScene();
          this.hasDragged = false;
        }
      }

      if (
        this.$el.contains(target) &&
        (typeof target.className !== "string" ||
          target.className.indexOf(this.inputSlotClassName) === -1)
      ) {
        this.linking = false;
        this.tempLink = null;
        this.linkStartData = null;
      }
    },
    handleWheel(e) {
      const target = e.target || e.srcElement;
      if (this.$el.contains(target)) {
        // if (e.preventDefault) e.preventDefault()

        let deltaScale = Math.pow(1.1, e.deltaY * -0.01);
        // console.log(deltaScale)
        this.scale *= deltaScale;

        if (this.scale < this.minScale) {
          this.scale = this.minScale;
          return;
        } else if (this.scale > this.maxScale) {
          this.scale = this.maxScale;
          return;
        }

        let zoomingCenter = {
          x: this.mouseX,
          y: this.mouseY,
        };

        let deltaOffsetX = (zoomingCenter.x - this.centerX) * (deltaScale - 1);
        let deltaOffsetY = (zoomingCenter.y - this.centerY) * (deltaScale - 1);

        this.centerX -= deltaOffsetX;
        this.centerY -= deltaOffsetY;

        this.updateScene();
      }
    },
    // Processing
    getConnectionPos(block, slotNumber, isInput) {
      if (!block || slotNumber === -1) {
        return undefined;
      }

      let x = 0;
      let y = 0;

      x += block.position[0];
      y += block.position[1];
      // console.log(this.optionsForChild)
      // console.log(isInput)

      // y += this.optionsForChild.titleHeight

      if (isInput && block.inputs.length > slotNumber) {
        if (block.inputs.length === 1) {
          x += this.optionsForChild.width / 2;
        } else {
          x += this.optionsForChild.width / 2 - (block.inputs.length * 10) / 2;
          x += 20 * slotNumber;
        }
      } else if (!isInput && block.outputs.length > slotNumber) {
        if (slotNumber === 0) {
          x += this.optionsForChild.width / 2;
          y += 50;
        }
        if (slotNumber === 1) {
          x += this.optionsForChild.width;
          y += 25;
        }
        if (slotNumber === 2) {
          y += 25;
        }
      } else {
        console.error(
          "slot " + slotNumber + " not found, is input: " + isInput,
          block
        );
        return undefined;
      }

      // (height / 2 + blockBorder + padding)
      // y += (20 / 2 + 1 + 2)
      //  + (height * slotNumber)
      // y += (16 * slotNumber)

      x *= this.scale;
      y *= this.scale;

      x += this.centerX;
      y += this.centerY;

      return { x, y };
    },
    // Linking
    linkingStart(block, slotNumber) {
      this.linkStartData = { block, slotNumber };
      let linkStartPos = this.getConnectionPos(
        this.linkStartData.block,
        this.linkStartData.slotNumber,
        false
      );
      this.tempLink = {
        x1: linkStartPos.x,
        y1: linkStartPos.y,
        x2: this.mouseX,
        y2: this.mouseY,
      };

      this.linking = true;
    },
    linkingStop(targetBlock, slotNumber) {
      if (this.linkStartData && targetBlock && slotNumber > -1) {
        const {slotNumber: originSlot, block: { id: originID } } = this.linkStartData;
        const targetID = targetBlock.id;
        const targetSlot = slotNumber;

        // console.log(originID, originSlot)
        // console.log(targetID, targetSlot)

        this.links = this.links.filter((line) => {
          return !(
            line.targetID === targetID
            && line.targetSlot === targetSlot
            && line.originID === originID
            && line.originSlot === originSlot
          ) && !(line.originID === originID && line.targetID === targetID);
        });

        let maxID = Math.max(
          0,
          ...this.links.map(function (o) {
            return o.id;
          })
        );

        if (this.linkStartData.block.id !== targetBlock.id) {
          this.links.push({
            id: maxID + 1,
            originID: this.linkStartData.block.id,
            originSlot: this.linkStartData.slotNumber,
            targetID: targetBlock.id,
            targetSlot: slotNumber,
          });
          // console.log("adddd");
          console.log(this.links);
          this.updateScene();
        }
      }

      this.linking = false;
      this.tempLink = null;
      this.linkStartData = null;
    },
    linkingBreak(targetBlock, slotNumber) {
      if (targetBlock && slotNumber > -1) {
        let findLink = this.links.find((value) => {
          return (
            value.targetID === targetBlock.id && value.targetSlot === slotNumber
          );
        });

        if (findLink) {
          let findBlock = this.blocks.find((value) => {
            return value.id === findLink.originID;
          });

          this.links = this.links.filter((value) => {
            return !(
              value.targetID === targetBlock.id &&
              value.targetSlot === slotNumber
            );
          });

          this.linkingStart(findBlock, findLink.originSlot);

          this.updateScene();
        }
      }
    },
    removeLink(linkID) {
      this.links = this.links.filter((value) => {
        return !(value.id === linkID);
      });
    },
    // Blocks
    addNewBlock(nodeName, x, y) {
      let maxID = Math.max(
        0,
        ...this.blocks.map(function (o) {
          return o.id;
        })
      );

      let node = this.nodes.find((n) => {
        return n.name === nodeName;
      });

      if (!node) {
        return;
      }
      let block = this.createBlock(node, maxID + 1);

      // if x or y not set, place block to center
      if (x === undefined || y === undefined) {
        x = (this.$el.clientWidth / 2 - this.centerX) / this.scale;
        y = (this.$el.clientHeight / 2 - this.centerY) / this.scale;
      } else {
        x = (x - this.centerX) / this.scale;
        y = (y - this.centerY) / this.scale;
      }

      block.position[0] = x;
      block.position[1] = y;
      this.blocks.push(block);

      this.updateScene();
    },
    createBlock(node, id) {
      let inputs = [];
      let outputs = [];
      let values = {};

      node.fields.forEach((field) => {
        if (field.attr === "input") {
          inputs.push({
            name: field.name,
            label: field.label || field.name,
          });
        } else if (field.attr === "output") {
          outputs.push({
            name: field.name,
            label: field.label || field.name,
          });
        } else {
          if (!values[field.attr]) {
            values[field.attr] = {};
          }

          let newField = merge({}, field);
          delete newField["name"];
          delete newField["attr"];

          if (!values[field.attr][field.name]) {
            values[field.attr][field.name] = {};
          }

          values[field.attr][field.name] = newField;
        }
      });

      return {
        id: id,
        position: [0,0],
        selected: false,
        name: node.name,
        title: node.title || node.name,
        inputs: inputs,
        outputs: outputs,
        values: values,
      };
    },
    deselectAll(withoutID = null) {
      this.blocks.forEach((value) => {
        if (value.id !== withoutID && value.selected) {
          this.blockDeselect(value);
        }
      });
    },
    // Events
    blockSelect(block) {
      block.selected = true;
      this.selectedBlock = block;
      this.deselectAll(block.id);
      this.$emit("blockSelect", block);
    },
    blockDeselect(block) {
      block.selected = false;

      if (block && this.selectedBlock && this.selectedBlock.id === block.id) {
        this.selectedBlock = null;
      }

      this.$emit("blockDeselect", block);
    },
    blockDelete(block) {
      if (block.selected) {
        this.blockDeselect(block);
      }
      this.links.forEach((l) => {
        if (l.originID === block.id || l.targetID === block.id) {
          this.removeLink(l.id);
        }
      });
      this.blocks = this.blocks.filter((b) => {
        return b.id !== block.id;
      });
      this.updateScene();
    },
    //
    prepareBlocks(blocks) {
      return blocks
        .map((block) => {
          let node = this.nodes.find((n) => {
            return n.name === block.name;
          });

          if (!node) {
            return null;
          }

          let newBlock = this.createBlock(node, block.id);

          newBlock = merge(newBlock, block, {
            arrayMerge: (d, s) => {
              return s.length === 0 ? d : s;
            },
          });

          return newBlock;
        })
        .filter((b) => {
          return !!b;
        });
    },
    prepareBlocksLinking(blocks, links) {
      if (!blocks) {
        return [];
      }

      let newBlocks = [];

      blocks.forEach((block) => {
        let inputs = links.filter((link) => {
          return link.targetID === block.id;
        });

        let outputs = links.filter((link) => {
          return link.originID === block.id;
        });

        block.inputs.forEach((s, index) => {
          // is linked
          block.inputs[index].active = inputs.some(
            (i) => i.targetSlot === index
          );
        });

        block.outputs.forEach((s, index) => {
          // is linked
          block.outputs[index].active = outputs.some(
            (i) => i.originSlot === index
          );
        });

        newBlocks.push(block);
      });

      return newBlocks;
    },
    importBlocksContent() {
      if (this.blocksContent) {
        this.nodes = merge([], this.blocksContent);
      }
    },
    importScene() {
      let scene = merge(this.defaultScene, this.scene);

      let blocks = this.prepareBlocks(scene.blocks);
      blocks = this.prepareBlocksLinking(blocks, scene.links);

      // set last selected after update blocks from props
      if (this.selectedBlock) {
        let block = blocks.find((b) => this.selectedBlock.id === b.id);
        if (block) {
          block.selected = true;
        }
      }

      this.blocks = blocks;
      this.links = merge([], scene.links);

      let container = scene.container;
      if (container.centerX) {
        this.centerX = container.centerX;
      }
      if (container.centerY) {
        this.centerY = container.centerY;
      }
      if (container.scale) {
        this.scale = container.scale;
      }
    },
    exportScene() {
      let clonedBlocks = merge([], this.blocks);
      let blocks = clonedBlocks.map((value) => {
        delete value["inputs"];
        delete value["outputs"];
        delete value["selected"];

        return value;
      });

      return {
        blocks: blocks,
        links: this.links,
        container: this.container,
      };
    },
    updateScene() {
      this.$emit("update:scene", this.exportScene());
    },
  },
  watch: {
    blocksContent() {
      this.importBlocksContent();
    },
    scene() {
      this.importScene();
    },
  },
};
</script>

<style lang="scss" scoped>
.vue-container {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}
.btn-zoom {
  display: grid;
  width: max-content;
  position: absolute;
  bottom: 0px;
  right: 0px;
  i {
    font-size: large;
    cursor: pointer;
    border: solid 2px #65b9f4;
    color: #65b9f4;
  }
}
</style>
