<template>
  <at-modal v-model="dialog" width="680">
    <div slot="header" style="text-align: center">
      <span>Загрузка модели</span>
    </div>
    <div class="row at-row no-gutter">
      <div class="col-16 models-list">
        <vue-custom-scrollbar class="scroll-area" :settings="settings">
          <ul class="loaded-list">
            <li
              v-for="(list, i) of preset"
              :key="`list_${i}`"
              @click="getModel(list)"
            >
              <i class="icon icon-file-text"></i>
              <span>{{ list.label }}</span>
            </li>
            <li
              v-for="(list, i) of custom"
              :key="`list_${i}`"
              @click="getModel(list)"
            >
              <i class="icon icon-file-text"></i>
              <span>{{ list.label }}</span>
              <div class="remove"></div>
            </li>
          </ul>
        </vue-custom-scrollbar>
      </div>
      <div class="col-8">
        <div class="model-arch">
          <div class="wrapper hidden">
            <div class="modal-arch-info">
              <div class="model-arch-info-param name">
                Name: <span>cifar10_conv2d</span>
              </div>
              <div class="model-arch-info-param input_shape">
                Input shape: <span>[32,32,3]</span>
              </div>
              <div class="model-arch-info-param datatype">
                Datatype: <span>2D</span>
              </div>
            </div>
            <div class="model-arch-img">
              <img
                alt=""
                width="100"
                height="200"
                src="data:image/png;base64,"
              />
            </div>
            <div class="model-save-arch-btn"><button>Загрузить</button></div>
          </div>
        </div>
      </div>
    </div>
    <div slot="footer"></div>
  </at-modal>
</template>

<script>
import vueCustomScrollbar from "vue-custom-scrollbar";
import { mapGetters } from "vuex";
export default {
  name: "ModalWindowLoadModel",
  components: {
    vueCustomScrollbar,
  },
  data: () => ({
    lists: [],
    settings: {
      suppressScrollY: false,
      suppressScrollX: true,
      wheelPropagation: false,
    },
  }),
  computed: {
    ...mapGetters({}),
    preset() {
      return this.lists[0]?.models || [];
    },
    custom() {
      return this.lists[1]?.models || [];
    },
    dialog: {
      set(value) {
        this.$store.dispatch("modeling/setDialog", value);
      },
      get() {
        return this.$store.getters["modeling/getDialog"];
      },
    },
  },
  methods: {
    CloseModalWindow() {
      this.$emit("CloseModalWindow", false);
    },
    async load() {
      const data = await this.$store.dispatch("axios", { url: "/modeling/models/"});
      if (data) {
        this.lists = data;
      }
    },
    async getModel({ name, is_terra }) {
      const req = {
        url: "/api/v1/modeling/models/",
        data: {
          model_file: name,
          is_terra,
        },
      };
      const data = await this.$store.dispatch("axios", req);
      console.log(data);
    },
  },
  watch: {
    dialog: {
      handler(value) {
        console.log(value);
        if (value) {
          this.load();
        }
      },
    },
  },
};
</script>

<style scoped>
.scroll-area {
  /* position: relative; */
  margin: auto;
  height: 350px;
}

/* modal-window */

.icon {
  font-size: 20px;
}
.loaded-list > li {
  padding: 7px 10px;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.loaded-list > li > i {
  color: #2b5278;
}
.loaded-list > li > span {
  line-height: 1;
  padding: 10px 10px;
  font-size: 0.875rem;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}
.models-list li:hover,
.models-list li:hover {
  color: #65b9f4;
}

.loaded-list > li > .remove {
  display: block;
  width: 26px;
  height: 26px;
  margin: -4px 0 0 0;
  position: relative;
  float: right;
  right: 4px;
  cursor: pointer;
  user-select: none;
  border-radius: 2px;
  transition: background-color 0.3s ease-in-out;
}
.loaded-list > li > .remove:before {
  display: block;
  content: "";
  width: 14px;
  height: 14px;
  margin: -7px 0 0 -7px;
  position: relative;
  left: 50%;
  top: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.loaded-list > li > .remove:hover {
  background: rgba(255, 255, 255, 0.2);
}
.model-arch {
  padding: 5px;
}
.model-save-arch-btn {
  padding-top: 20px;
}
.modal-arch-info {
  padding-left: 10px;
}
.model-arch-img {
  padding: 5px;
}
.modal-arch-info > div {
  color: #a7bed3;
}
.modal-arch-info > div > span {
  color: #ffffff;
  font-size: 0.85rem;
}

.close {
  width: 44px;
  height: 44px;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}
.close:after {
  display: block;
  content: "";
  width: 40%;
  height: 2px;
  margin: -1px 0 0 0;
  position: absolute;
  left: 30%;
  top: 50%;
  border-radius: 1px;
  background-color: #a7bed3;
  transform: rotate(45deg);
}
.close:before {
  display: block;
  content: "";
  width: 40%;
  height: 2px;
  margin: -1px 0 0 0;
  position: absolute;
  left: 30%;
  top: 50%;
  border-radius: 1px;
  transform: rotate(-45deg);
  background-color: #a7bed3;
}
</style>